import React from 'react';
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Box,
  Button,
  Chip,
  Avatar,
} from '@mui/material';
import {
  Home as HomeIcon,
  Settings as SettingsIcon,
  ArrowBack as ArrowBackIcon,
} from '@mui/icons-material';

export default function Navigation({ 
  currentView, 
  onNavigate, 
  selectedItems = [],
  onClearSelected,
  onBack 
}) {
  const handleSpeak = () => {
    const text = selectedItems.map(item => item.name).join(', ');
    const utterance = new SpeechSynthesisUtterance(text);
    utterance.lang = 'tr-TR';
    window.speechSynthesis.speak(utterance);
  };

  return (
    <AppBar position="sticky" sx={{ backgroundColor: 'rgba(255, 255, 255, 0.9)' }}>
      <Toolbar>
        {onBack ? (
          <IconButton edge="start" color="primary" onClick={onBack}>
            <ArrowBackIcon />
          </IconButton>
        ) : (
          <IconButton edge="start" color="primary" onClick={() => onNavigate('home')}>
            <HomeIcon />
          </IconButton>
        )}

        <Box sx={{ flexGrow: 1, display: 'flex', alignItems: 'center', gap: 1, overflowX: 'auto', py: 1 }}>
          {selectedItems.map((item) => (
            <Chip
              key={item.id}
              avatar={<Avatar alt={item.name} src={item.image} />}
              label={item.name}
              sx={{ 
                backgroundColor: 'white',
                '& .MuiChip-label': { color: 'text.primary' }
              }}
            />
          ))}
        </Box>

        {selectedItems.length > 0 && (
          <>
            <Button 
              color="success" 
              onClick={handleSpeak}
              sx={{ mx: 1 }}
            >
              Oku
            </Button>
            <Button 
              color="error" 
              onClick={onClearSelected}
            >
              Temizle
            </Button>
          </>
        )}

        <IconButton color="primary" onClick={() => onNavigate('settings')}>
          <SettingsIcon />
        </IconButton>
      </Toolbar>
    </AppBar>
  );
}
