import React, { useState } from 'react';
import { ThemeProvider, CssBaseline, Container, Box, Grid, Card, CardContent, CardMedia, Typography } from '@mui/material';
import { lightTheme, darkTheme } from './styles/theme';
import { SettingsProvider, useSettings } from './contexts/SettingsContext';
import Navigation from './components/Navigation';
import CategoryGrid from './components/CategoryGrid';
import FavoriteCards from './components/FavoriteCards';
import UserSettings from './components/UserSettings';
import { categories } from './data/categories';

function CategoryCard({ category, onClick }) {
  const { settings } = useSettings();
  const cardSize = settings.cardSize === 'large' ? 200 : 
                  settings.cardSize === 'medium' ? 160 : 120;

  return (
    <Card 
      sx={{ 
        width: cardSize * 1.5, 
        cursor: 'pointer',
        transition: 'transform 0.2s',
        '&:hover': {
          transform: 'scale(1.05)',
        }
      }}
      onClick={() => onClick(category)}
    >
      <CardMedia
        component="img"
        height={cardSize}
        image={category.icon}
        alt={category.title}
        sx={{ objectFit: 'contain', p: 1 }}
      />
      <CardContent>
        <Typography 
          gutterBottom 
          variant="h6" 
          component="div"
          align="center"
          sx={{ 
            fontSize: settings.fontSize === 'large' ? '1.5rem' : 
                     settings.fontSize === 'medium' ? '1.25rem' : '1rem'
          }}
        >
          {category.title}
        </Typography>
      </CardContent>
    </Card>
  );
}

function AppContent() {
  const [selectedItems, setSelectedItems] = useState([]);
  const [currentView, setCurrentView] = useState('home');
  const [selectedCategory, setSelectedCategory] = useState(null);
  const { settings } = useSettings();

  const handleCardSelect = (item) => {
    setSelectedItems(prev => {
      const exists = prev.find(i => i.id === item.id);
      if (exists) {
        return prev.filter(i => i.id !== item.id);
      }
      return [...prev, item];
    });
  };

  const handleSetSelectedItems = (items) => {
    setSelectedItems(items);
  };

  const handleCategoryClick = (category) => {
    setSelectedCategory(category);
  };

  const handleBackToCategories = () => {
    setSelectedCategory(null);
  };

  return (
    <ThemeProvider theme={settings.theme === 'dark' ? darkTheme : lightTheme}>
      <CssBaseline />
      <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
        <Navigation 
          onNavigate={setCurrentView} 
          selectedItems={selectedItems}
          onSelect={handleSetSelectedItems}
          onBack={selectedCategory ? handleBackToCategories : undefined}
        />
        
        <Container sx={{ flexGrow: 1, py: 2 }}>
          {currentView === 'home' && !selectedCategory && (
            <Grid container spacing={3} justifyContent="center">
              {categories.map(category => (
                <Grid item key={category.id}>
                  <CategoryCard 
                    category={category}
                    onClick={handleCategoryClick}
                  />
                </Grid>
              ))}
            </Grid>
          )}

          {currentView === 'home' && selectedCategory && (
            <CategoryGrid
              category={selectedCategory}
              onCardSelect={handleCardSelect}
              onBack={handleBackToCategories}
            />
          )}
          
          {currentView === 'favorites' && (
            <FavoriteCards onCardSelect={handleCardSelect} />
          )}
          
          {currentView === 'settings' && <UserSettings />}
        </Container>
      </Box>
    </ThemeProvider>
  );
}

export default function App() {
  return (
    <SettingsProvider>
      <AppContent />
    </SettingsProvider>
  );
}
